body {
  box-sizing: border-box;
  color: #484848;
  font-family: "FiraSans";
  font-weight: 400;
  font-size: 15px; }

.container {
  max-width: 1366px;
  margin: 32px auto; }

.main {
  height: 906px; }
  .main__bg {
    position: relative;
    background-image: url(../img/bg.jpg);
    background-repeat: no-repeat;
    background-position-y: -30px;
    display: block;
    margin-top: -50px; }
  .main__body {
    position: relative;
    height: 906px; }
  .main__content {
    position: absolute;
    top: 49.5vh;
    left: 37.5%;
    max-width: 343px;
    text-align: center;
    display: flex;
    flex-flow: column;
    align-items: center; }
  .main__sub-title {
    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
    color: #ffffff;
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    margin-bottom: 35px; }
  .main__scroll-body {
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 76px; }
  .main__scroll-tip {
    display: inline-block;
    border: dashed white 1px;
    padding: 11px;
    border-radius: 50px;
    cursor: pointer; }
  .main__scroll-tail {
    width: 2px;
    height: 56px;
    background-color: white; }

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 114px;
  z-index: 1;
  position: relative; }
  .menu__number {
    font-weight: 500;
    text-decoration: none;
    color: #484848; }
  .menu__ring-me {
    border: none;
    outline: none;
    background: none;
    font-size: 15px;
    font-weight: 500;
    color: #484848;
    font-family: "FiraSans";
    padding: 0;
    border-bottom: 1px dashed #484848;
    text-transform: uppercase;
    cursor: pointer; }

